import React, { useState } from 'react';
import Layout from '../components/layout';
import InnerPageTitle from '../components/utils/inner-page-title';
import {Link} from 'gatsby';
import { ethers } from "ethers";
import MetamaskIcon from '../assets/images/metamask.png';
import Constants from '../utils/constants';

declare var window: any;
var provider: any;
var mobile: boolean;

if(typeof window !== 'undefined' && window.ethereum) {
   provider =  new ethers.providers.Web3Provider(window.ethereum);
}
if(typeof window !== 'undefined') {
    var match = window.matchMedia("(max-width: 991px)");
    mobile = match.matches;
    match.addListener(() => {
        mobile = match.matches;
    });
}

const ClaimPage = () => { 
    const [walletAddress, setWalletAddress] = useState('');
    const [isConnected, setIsConnected] = useState(false);
    const [connectionMessage, setConnectionMessage] = useState('');
    const [cshirts, setCShirts] = useState([]);
    
    let connectWallet;
    if(typeof window !== 'undefined')
    {

    connectWallet = (async() => {
        if(window.ethereum) {
          try {
            await provider.send('eth_requestAccounts', []);
            const signer = provider.getSigner();
            const address = await signer.getAddress();      
            if(address) {
                setWalletAddress(address);
                setIsConnected(true);
                const tokens:string[] = [Constants.CSHIRTS_CONTRACTADDRESS]
                const contract = new ethers.Contract(Constants.CSHIRTS_CONTRACTADDRESS, Constants.CSHIRTS_CONTRACTABI, signer); 
                //const balances = await contract.balances([address], tokens);     
                debugger;        
            }    
            else {
              setWalletAddress('');
            }
          } 
          catch(err) {
            setConnectionMessage(err.message);
          }    
        } 
        else {
            setConnectionMessage("Please install Metamask");
        }
      });
    }
    const PageLayout = <Layout pageTitle="Non Fungible Cup: Claim your wins" container="inner-page">
        <InnerPageTitle title="Claim wins" subtitle="Connect your wallet and check you wins" />
        <section className="tf-section tf-item tf-claim">
            <div className="container">
                <div className="row">
                    <div className={mobile == false || window.ethereum ? "col-md-12 center" : "hide"}>
                        <button className={isConnected == true ? "hide" : "tf-button btn-effect active"} onClick={connectWallet}>
                            <span className="boder-fade"></span>          
                            <span className="effect"><img src={MetamaskIcon} className="metamaskIcon" />Connect your wallet</span>
                        </button>                         
                        <span><strong>{connectionMessage}</strong></span><br />
                    </div>
                    <div className={mobile == true && !window.ethereum ? "col-md-12 center" : "hide"}>
                        <a href={Constants.METAMASK_MOBILE_CLAIMURL} className={isConnected == true ? "hide" : "tf-button btn-effect active"}>
                            <span className="boder-fade"></span>          
                            <span className="effect"><img src={MetamaskIcon} className="metamaskIcon" />Open Metamask</span>
                        </a>                 
                        <span><strong>{connectionMessage}</strong></span><br />
                    </div>             
                </div>  
                <div className={isConnected ? "row" : "hide"}>
                    <div className="col-md-12 center">
                        <h4>Your C-Shirts</h4>
                        <div className={isConnected ? "wallet" : "hide"}>Wallet connected: {walletAddress}</div>
                        <div className={isConnected && cshirts.length == 0 ? "claim-container" : "hide"}>
                            <div className="claim-container toUpper nodata">No C-Shirts found in your wallet</div>
                            <div>
                                <Link className="tf-button st2 btn-effect" to="/cshirts">
                                    <span className="boder-fade"></span>
                                    <span className="effect">Get a C-Shirt</span>
                                </Link>
                            </div>
                        </div>
                        <div className={isConnected && cshirts.length > 0 ? "claim-container" : "hide"}>
                            
                        </div>
                    </div>
                </div>              
            </div>
        </section>
    </Layout>
    return(
        PageLayout
    );
};

export default ClaimPage;